.warehousesWrap {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  .item{
    max-width: 230px;
    min-height: 260px;
    width: 100%;
    height: 100%;
    background: white;
    padding: 10px 15px;
    border-radius: 8px;
    box-shadow: 0px 4px 8px 0px rgba(34, 60, 80, 0.2);
    position: relative;
    overflow: hidden;
    margin-right: 30px;
    margin-bottom: 20px;
    transition: .3s;
    &:hover{
      box-shadow: 0px 4px 10px 0px rgba(34, 60, 80, 0.4);
      transform: scale(1.02);
    }
    .title{
      font-weight: 700;
      width: 100%;
      font-size: 16px;
      display: flex;
      align-items: center;
      position: relative;
      span{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .icon{
        margin-right: 7px;
        font-size: 20px;
        color: #00A65A;
      }
      &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        background: #333;
        bottom: -9px;
        opacity: .25;
      }
    }
    .body{
      margin-top: 15px;
      span {
        font-size: 16px;
        font-weight: 600;
      }
      ul {
        margin-top: 5px;
        padding-left: 13px;
        padding-bottom: 20px;
        li {
          list-style: none;
          font-size: 14px;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: space-between;
          span{
            font-size: 9px;
            font-weight: 700;
            background: #f39c12;
            color: white;
            width: 15px;
            height: 15px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            text-align: center;
          }
          &:before{
            content: '';
            width: 7px;
            height: 2px;
            background: #00A65A;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: -12px;
          }
        }
      }
      .empty{
        color: #a0a0a0;
        font-weight: 100;
      }
    }
    .bottom{
      position: absolute;
      bottom: 5px;
      width: 100%;
      left: 0;
      color: #00A65A;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .2s;
      .icon {
        font-size: 14px;
        margin-right: 3px;
      }
      &:hover {
        color: #1A2226;
      }
    }
  }
  .itemAdd{
    max-width: 230px;
    min-height: 260px;
    width: 100%;
    height: 100%;
    background: #c4c4c4;
    border-radius: 8px;
    box-shadow: 0px 4px 8px 0px rgba(34, 60, 80, 0.2);
    position: relative;
    overflow: hidden;
    margin-right: 30px;
    margin-bottom: 20px;
    transition: .3s;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    span{
      font-weight: 500;
      color: #4f4f4f;
    }
    .itemIcon{
      font-size: 70px;
      color: #ECF0F5;
      transition: .2s;
    }
    &:hover{
      box-shadow: 0px 4px 10px 0px rgba(34, 60, 80, 0.4);
      transform: scale(1.02);
    }
  }
}

.modalList{
  background: #ECF0F5;
  border-radius: 15px;
  box-shadow: 0 0 7px rgba(0,0,0,.35);
  ul {
    margin-top: 15px;
    padding: 10px 20px 10px 25px;
    li {
      list-style: none;
      position: relative;
      display: flex;
      align-items: center;
      //justify-content: space-between;
      .iconDelete{
        background: #9ea3a9;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        color: white;
        cursor: pointer;
        border-radius: 50%;
        font-size: 18px;
        transition: .2s;
        &:hover{
          background: #bd102d;
        }
      }
      span{
        font-size: 9px;
        font-weight: 700;
        background: #f39c12;
        color: white;
        width: 15px;
        height: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        text-align: center;
        margin-left: 15px;
      }
      &:before{
        content: '';
        width: 7px;
        height: 2px;
        background: #00A65A;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -12px;
      }
    }
  }
}

@media screen and (max-width: 460px) {
  .warehousesWrap {
    .item {
      margin-right: 0;
    }
  }
}